<template>
	<view>
		<input type="text" placeholder="手机号" v-model='phone' class="phone-in" placeholder-style="color:#aec5d8"/>
		<view class="code-area">
			<input type="text" placeholder="验证码" v-model='code' class="code-in" placeholder-style="color:#aec5d8"/>
			<button @click="sendCode(phone)" class="send-code-button">发送验证码</button>
		</view>
		<button @click="login(phone,code)" class="login-button">登录</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone:null,
				code:null
			}
		},
		methods: {
			authentication:(phone,code) => {
				axios.get(`/captcha/verify?phone=${this.phone}&captcha=${this.code}`).then(res => {
					console.log(res.data)
				})
			},
			
			sendCode:(phone) => {
				if(phone){
					uni.showModal({
						title:'注意!',
						content:"手机号不能为空",
						confirmText:"确认",
						cancelText:"取消"
					})
					return
				}
				axios.get(`/captcha/sent?phone=${phone}`).then(res => {
					if(res.data.code === 200){
						uni.showModal({
							title:'通知',
							content:'已发送验证码',
							confirmText:'确认'
						})
					}
				})
			}
		}
	}
</script>

<style>
@import url('login.css');
</style>
